<template>
  <view class="work-container">
	<uni-search-bar @confirm="search" placeholder="请输入搜索内容" v-model="params.workDetails" radius="100" @clear="clear">
	</uni-search-bar>
    <view class="uni-padding-wrap uni-common-mt">
		<uni-segmented-control :current="current" :values="tabs" style-type="text"
			active-color="#007aff" @clickItem="onClickItem" />
	</view>
	<view class="content">
		<Record v-if="current == 0" :list="list" @click="navigateTo"></Record>
		<Record v-if="current == 1" :list="list" @click="navigateTo"></Record>
		<uni-pagination :total="total" :current="params.pageNum" :pageSize="params.pageSize" :showIcon="true" @change="pageChange" />
		<uni-fab ref="fab" :pattern="pattern" horizontal="left" vertical="bottom"
			direction="horizontal" @fabClick="fabClick" />
	</view>
  </view>
</template>

<script>
import { getWorkLogList,getWorkLogNoList } from "@/api/work/archive/worklog"
import Record from './record'
export default {
	components: {
		Record,
	},
  data () {
    return {
		tabs: ['计量', '不计量'],
		current: 0,
		// 总条数
		total: 0,
		params: {
			workDetails:'',
			pageNum: 0,
			pageSize: 6
		},
		list: [],
		pattern: {
			color: '#7A7E83',
			backgroundColor: '#fff',
			selectedColor: '#007AFF',
			buttonColor: '#007AFF',
			iconColor: '#fff'
		},
    }
  },
  created () {
  	this.getList();
  },
  onShow(){
  	this.getList()
  },
  
  methods: {
	// 搜索框
	search() {
		this.params.pageNum = 0;
		this.getList()
	},
	pageChange(e){
		this.params.pageNum = e.current;
		this.getList()
	},
	// 加载数据
	loadData () {
	  this.params.pageNum += 1;
	  this.getList();
	},
	cancel(res) {
		uni.showToast({
			title: '点击取消，输入值为：' + res.value,
			icon: 'none'
		})
	},
	clear() {
		this.params.pageNum = 0;
		this.params.workDetails ="";
		this.getList();
	},
	onClickItem(e) {
		if (this.current !== e.currentIndex) {
			this.current = e.currentIndex
			this.params.pageNum = 0;
			this.getList();
		}
	},
	getList () {
		const app = this
		if(app.current == 0){
		  // 计量
		  getWorkLogList(this.params).then(res => {
			app.list = res.rows;
			app.total = res.total;
		  })
		}else{
		  // 不计量
		  getWorkLogNoList(this.params).then(res => {
			app.list = res.rows;
			app.total = res.total;
		  })
		}
	},
	// 悬浮按钮
	fabClick() {
		if(this.current == 0)
			uni.navigateTo({ url: '/pages/work/archive/worklog/edit' })
		else
			uni.navigateTo({ url: '/pages/work/archive/worklog/edit-no' })
	},
	navigateTo (workLog) {
		if (workLog) {
			if(this.current == 0)
				uni.navigateTo({ url: '/pages/work/archive/worklog/edit?id=' + workLog.id })
			else
				uni.navigateTo({ url: '/pages/work/archive/worklog/edit-no?id=' + workLog.id })
		} else {
			if(this.current == 0)
				uni.navigateTo({ url: '/pages/work/archive/worklog/edit' })
			else
				uni.navigateTo({ url: '/pages/work/archive/worklog/edit-no' })
		}
	}
  }
}
</script>

<style lang="scss" scoped>
.list-item {
  padding: 20rpx 0;
  border-bottom: 0.5px solid #ccc;
}
</style>